<template>
   <div class="footer">
      <ul>
         <li>哈哈</li>
         <li>哈哈</li>
         <li>哈哈</li>
         <li>哈哈</li>
      </ul>
   </div>
</template>

<script>
export default {
   data(){
      return {
         active: 0
      }
   }
}
</script>

<style lang="scss" scoped>
.footer {
   height: 100px;
   background: #ffffff;
   border-top: 1px solid #ccc;
   position: fixed;
   left: 0;
   right: 0;
   bottom: 0;
   box-sizing: content-box;
   padding-bottom: constant(safe-area-inset-bottom);
   padding-bottom: env(safe-area-inset-bottom);
   overflow: hidden;
   ul {
      width: 100%;
      overflow: hidden;
      height: 100%;
      display: flex;
      justify-content: space-between;
      li {
         flex: 1 0 auto;
         display: flex;
         justify-content: center;
         align-items: center;
      }
   }
}
</style>


